/*
@styleguide

@title Button

Use the button classes on and `<a>`, `<button>`, `<input>` elements.

<button class="button button--large button--red">Red Button</button>

    <button class="button button--large button--red">Red Button</button>

<button class="button button--large button--blue">Blue Button</button>

    <button class="button button--large button--blue">Blue Button</button>
*/
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  cursor: pointer; }

.button--large {
  width: 140px;
  height: 40px;
  font-size: 14px; }

.button--red {
  color: #fff;
  background-color: red; }

.button--primary {
  color: #fff;
  background-color: rebeccapurple; }

.new-recipe-button, .add-step, .search-button, .add-ingredient {
  position: fixed;
  z-index: 99999;
  bottom: 0;
  right: 0;
  margin: 3rem;
  font-size: 3rem;
  border-radius: 100rem;
  padding: 1rem 2rem;
  border: 3px solid transparent;
  color: white;
  background-color: rebeccapurple;
  box-shadow: 1px 1px 4px 2px #b3b3b3;
  transition: .16s; }
  .new-recipe-button:hover, .add-step:hover, .search-button:hover, .add-ingredient:hover {
    box-shadow: 2px 2px 4px 3px #b3b3b3;
    cursor: pointer; }
  .new-recipe-button:active, .add-step:active, .search-button:active, .add-ingredient:active, .new-recipe-button:hover, .add-step:hover, .search-button:hover, .add-ingredient:hover {
    color: rebeccapurple;
    background-color: white;
    border: 3px solid rebeccapurple; }
  .new-recipe-button:focus, .add-step:focus, .search-button:focus, .add-ingredient:focus {
    outline: 0; }

.add-step, .search-button, .add-ingredient {
  z-index: 0;
  position: relative;
  margin: 0;
  border-radius: 1rem;
  font-size: 1rem;
  padding: 0 1rem; }

.search-button {
  background-color: white;
  color: rebeccapurple;
  font-weight: bold;
  border-radius: 5px;
  border: 3px solid transparent;
  box-shadow: 0 0 0 0; }
  .search-button:hover {
    box-shadow: 0 0 0 0;
    border: 3px solid transparent;
    background-color: tomato;
    color: white; }

.recipe-list {
  list-style-type: none;
  margin: 0 auto;
  display: flex;
  justify-content: center; }
  .recipe-list ul {
    padding: 0;
    list-style-type: none;
    width: 100%; }
  .recipe-list li {
    display: block;
    list-style: none;
    margin-top: 1rem; }

.recipe, .new-recipe {
  width: 100%;
  min-width: 300px;
  max-width: 600px;
  text-align: left;
  border-radius: 1px;
  box-shadow: 2px 2px 4px 2px #b3b3b3;
  transition: .16s; }
  .recipe:hover, .new-recipe:hover {
    box-shadow: 3px 3px 4px 4px #b3b3b3;
    cursor: pointer; }

.recipe-header, .new-recipe-header {
  width: 100%;
