.new-recipe input {
  box-sizing: border-box;
  font-size: .8rem;
  padding: 5px;
  margin: .5rem 0;
  border-radius: 5px;
  outline: none;
  transition: .1s;
  border-style: solid;
  border: none;
  box-shadow: inset 0px 0px 1px 1px rebeccapurple; }
  .new-recipe input:focus {
    box-shadow: inset 0px 0px 1px 2px rebeccapurple; }
  .new-recipe input:hover {
    cursor: pointer; }
  @media only screen and (min-width: 500px) {
